.app {
  background-color: #232927;
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
}

.board {
  /*width: calc(90vh - 10px);*/
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(4, 1fr);
  width: 60%;
}

.card-outer {
  perspective: 1000px;
  width: calc((90vh - 50px) / 4);
  height: calc((100vh - 50px) / 3);
  margin: auto;
}

.card {
  border-radius: 10px;
  position: relative;
  width: 100%;
  height: 100%;
  transform: rotateY(180deg);
  transform-style: preserve-3d;
  transition: transform 1s;
  border: 4px solid #232927;
}

.card img {
  display: block;
  width: calc((90vh - 50px) / 4);
  height: calc((100vh - 50px) / 3);
}

.card .back {
  background-color: #D3C7A6;
  transform: rotateY(180deg);
}

.card .front,
.card .back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  overflow: hidden;
  border-radius: 8px;
  text-align: center;
  line-height: 10px;
  font-size: 0.6em;
}

.card-outer.flipped .card {
  transform: rotateY(0);
  border: 4px solid #131718;
  transition: 1s;
}

.card-outer.clicked .card {
  border: 4px solid #1f6f83;
  transition: 1s;
}

.header {
  display: flex;
  color: #D7D9C9;
  justify-content: space-between;
  align-items: center;
  font-size: 1em;
  width: 100%;
}

.player {
  display: flex;
  flex-flow: row;
  width: 30%;
}

.player.two {
  text-align: end;
}

.player-name {
  display: flex;
  justify-content: space-around;
}

.player-name input {
  background-color: #232927;
  border: none;
  color: #D7D9C9;
  font-size: 1em;
  text-align: center;
  outline: none;
  width: 98%;
  padding: 1%;
}

.player.one input {
  text-align: start;
}

.player.two input {
  text-align: end;
}

.header h1 {
  font-size: 1em;
}

.rules {
  text-align: center;
  width: 50%;
}

.player-icon {
  height: 20px;
  min-width: 20px;
  border-radius: 100px;
  margin: 7px 4%;
}

.one .player-icon {
  background-color: #CA3323;
}

.two .player-icon {
  background-color: #569164;
}

.player-info {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  line-height: 0%;
  width: 100%;
  margin: 2%;
}

.choice-container {
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  width: 19%;
}

.choice-buttons {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

.choice-buttons button {
  color: #131718;
  background-color: #D7D9C9;
  margin: 1px;
  justify-content: center;
  border: none;
  padding: 6px;
  text-align: center;
  text-decoration: none;
  font-size: 12px;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  border: 3px solid #232927;
  transition: 1s;
}

.choice-buttons button:hover {
  background-color: #D3C7A6;
  transition: 0.8s;
}

.choice-buttons button.clicked {
  border: 3px solid #1f6f83;
  transition: 1s;
}

.history {
  color: #D7D9C9;
  width: 100%;
}

.rules button {
  color: #131718;
  background-color: #D7D9C9;
  margin: 1px;
  justify-content: center;
  border: none;
  padding: 6px;
  text-align: center;
  text-decoration: none;
  font-size: 12px;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  border: 4px solid #232927;
  transition: 1s;
}